import React, {
  useCallback,
  useState,
  useRef
} from 'react'
import styles from './levelOne.module.sass'
import gsap from 'gsap'
import img1 from 'assets/images/optimization/01.jpg'
import img2 from 'assets/images/optimization/02.jpg'

import Magnifier from 'react-magnifier'

export default function LevelOne() {
  const isMounted = useRef(false)
  const [showImg, setShowImg] = useState(img1)

  const btnText = useRef(null)

  const textList = [
    <p key={'1'} className={styles.text}> <span className={styles.textDeco}>webm</span> </p>,
    <p key={'2'} className={styles.text}> <span className={styles.textDeco}>webp</span> </p>, 
    <p key={'3'} className={styles.text}></p>,
  ]

  const onClickNextLevel = useCallback(() => {
    if (isMounted.current) return
    isMounted.current = true

    let tl = gsap.timeline()
    tl.to('#btn',{
      duration: 0,
      borderColor: '#177535',
      onComplete: () => {
        btnText.current.textContent = ' ing...'
      },
    }).to('#btnText',{
      duration: 0,
      color: '#fff',
    }).to('#btnBg',{
      duration: 2,
      width: '100%',
      ease: 'power2.out',
      onComplete: () => {
        btnText.current.textContent = ''
        setShowImg(img2)
      },
    }).to('#other',{
      delay: 1,
      visibility: 'visible',
    }).to('#other',{
      opacity: 1,
      duration: .3,
    })
  }, [])

  return (
    <div className={styles.root}>
      <div className={styles.left}>
        <div className={styles.img}>
          <Magnifier src={showImg} width={600} height={335} zoomFactor={2.5} className={styles.imgDecorate} mgWidth={200} mgHeight={200} />;
        </div>
        <div id={'btn'} className={styles.btn} onClick={onClickNextLevel}>
          <div id={'btnBg'} className={styles.btnBg} />
          <p id={'btnText'} ref={btnText} className={styles.btnText}> </p>   
        </div>
        <div id={'other'} className={styles.other}>
          <p className={styles.arrow}></p>
          <a className={styles.link} target="_blank" href="https://pagespeed.web.dev/analysis/https-vertexplay-com-zh-Hans/yn76il0iav?form_factor=desktop" rel="noreferrer">{'VPv2_'}</a>
        </div>
        
      </div>
      <div className={styles.textBox}>
        <p className={styles.title}>{''}</p>
        {/* <p className={styles.text}>
          {' Frame ...'}
        </p> */}
        <div className={styles.texts}>
          {textList.map((cur, index) => cur)}
        </div>
      </div>
    </div>
  )
}
